























































import {request, getQueryString} from 'common/js/common'
export default {
	data() {
		return {
			latitude: '',
			longitude: '',
			mapContext: {},
			callout: {
				bgColor: 'red'
			},
			covers: [
				{
					latitude: 27.9539300000,
					longitude: 120.5989070000,
					iconPath: '/static/img/icon/Location.png',
					width: '60rpx',
					height: '60rpx',
					title: '测试',
					callout: this.callout
				}
			],
			id: null,
			type: null
		};
	},
	onShow(){
		this.mapContext = uni.createMapContext('map',this)
		uni.getLocation({
		  type: 'gcj02',
		  success: res => {
			this.latitude = res.latitude
			this.longitude = res.longitude
			this.covers.push({
				latitude: this.latitude,
				longitude: this.longitude,
				iconPath: '/static/img/icon/Location.png',
				width: '60rpx',
				height: '60rpx',
				title: '我的位置'
			})
		  }
		})
	},
	onLoad(options){
		let link = decodeURIComponent(options.q)
		let paramArr = link.split('=');
		console.log(paramArr.length)
		if (paramArr.length == 2){
			var params = paramArr[1].split('_');
			this.id = params[0];
			this.type =params[1];
		}     
	},
	methods: {
		changePosition(){
			this.mapContext.moveToLocation()
		},
		openScan(){
			uni.scanCode({
				onlyFromCamera: true,
				success: (res) => {
					this.navicateToPath(res.path)
				}
			});
		},
		navicateToPath(url){
			uni.navigateTo({
				url: '/'+ url
			});
		},
		chooseLocation(){
			uni.openLocation({
				latitude: 27.9539300000,
				longitude: 120.5989070000,
				success: function () {
					console.log('success');
				}
			});
		}
	}
};
